import React from 'react';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';
import Home from '../Components/Home';
import AdSubmit from '../Components/adSubmit';
import login from '../Components/login';
import register from '../Components/register';
import profile from '../Components/profile';
import productCategoryDisplay from '../Components/products';
import adDetail from '../Components/adDetail'
import logo from '../assets/images/olx.png';

let adBtn = {
    padding: '10px',
    float: 'right'
}
let logoStyle = {
    width: '70px',
    height: '70px',
}
let secondUL_head = {
    listStyle: 'none',
    margin: '10px'
}
let loginStyle ={
    
    marginRight: '10px',
    color: '#f0ad4e'
}


const Routes = () => (
    <div>
    <div className="container">
        <div className="row">
            <div className="col-md-10 col-md-offset-1">
                <Router>
                    <div>
                        <nav className="navbar navbar-default"  style={{backgroundColor: 'white', border: 'none'}}>
                            <div className="container-fluid">
                                <div className="navbar-header">
                                    <a className="navbar-brand" href="/"><img alt="logo" src={logo} style={logoStyle} /> </a>
                                    <button type="button" className="navbar-toggle" data-toggle="collapse" data-target="#myNavbar">
                                        <span className="icon-bar"></span>
                                        <span className="icon-bar"></span>
                                        <span className="icon-bar"></span>
                                    </button>

                                </div>
                                <div className="collapse navbar-collapse" id="myNavbar">
                                    <ul className="nav navbar-nav">
                                        <li><Link to='/'><h3>Pakistan's Largest Marketplace</h3></Link></li>
                                    </ul>
                                    <ul style={secondUL_head}>
                                        <li style={adBtn}><Link to='/AdSubmit' className="btn btn-warning btn-lg">Submit an Ad</Link></li>
                                        <li style={adBtn}><Link to='/login' style={loginStyle} className="btn btn-defaul btn-lg"><span className="glyphicon glyphicon-user"></span>&nbsp;My Account</Link></li>
                                    </ul>

                                </div>
                            </div>
                        </nav>
                        <div className="row">
                            <div className="col-md-12">
                                <Route exact path='/' component={Home} />
                                <Route exact path='/AdSubmit' component={AdSubmit} />
                                <Route exact path='/login' component={login} />
                                <Route exact path='/register' component={register} />
                                <Route exact path='/profile' component={profile} />
                                <Route path='/category/:cat_id' component={productCategoryDisplay} />
                                <Route path='/adDetail/:id' component={adDetail} />
                            </div>
                        </div>
                    </div>
                </Router>
            </div>
        </div>
    </div>
    
    <hr />

    <div className="continer-fluid">
    image
    <div className="container">Footer</div>
    </div>
    </div>
    
)

export default Routes;


